* {
	margin: 0;
	padding: 0;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}
html,
body,
#root,
#root > * {
	height: 100%;
}

html {
	font-size: 100%;
}

a {
	text-decoration: none;
}
li {
	list-style: none;
}
img {
	max-width: calc(100% - 0);
	width: 100%;
}



